axios.defaults.withCredentials = true

export default class CreateUsers extends Component {
  constructor(props) {
    super(props)

    this.state = {
      code: 1,
      display: 'block',
    }
    axios.get('http://localhost:5000/users/userInfo').then((res) => {
      console.log(res.data.data)
      res.data.code === 0
        ? this.setState({ display: 'none' })
        : this.props.history.push('/user/login')
    })
  }

  cancel() {
    this.props.history.push('/')
  }

  logout() {
    //è°ƒç”¨åç«¯æ¥å£åˆ›å»ºuser
    axios.get('http://localhost:5000/users/logout').then((res) => {
      console.log(res.data)
      if (res.data.logout === "1") this.props.history.push('/')
    })
  }
  render() {
    return (
      <div>
        <div
          style={{
            zIndex: 1,
            backgroundColor: 'white',
            width: '930px',
            height: '500px',
            position: 'absolute',
            textAlign: 'center',
            paddingTop: '150px',
            display: this.state.display,
          }}
        >
          <Spin size='large' tip='Loading...' />
        </div>
        <h3 className='mt-3 mb-5'>ãƒ­ã‚°ã‚¢ã‚¦ãƒˆ</h3>
        <span className='form-group'>
          ãªã‚“ã§ã‚‚ã•ã„ã¨ã‹ã‚‰ãƒ­ã‚°ã‚¢ã‚¦ãƒˆã—ã¾ã™ã‹ï¼Ÿ
        </span>
        <div className='form-group'>
          <button
            onClick={this.cancel.bind(this)}
            className='btn btn-info'
            style={{ backgroundColor: 'lightgray', borderColor: 'lightgray' }}
          >
            ã‚­ãƒ£ãƒ³ã‚»ãƒ«
          </button>
          <button onClick={this.logout.bind(this)} className='btn btn-info'>
            ãƒ­ã‚°ã‚¢ã‚¦ãƒˆ
          </button>
        </div>
      </div>
    )
  }
}
